import React, { useState } from 'react';
import s from './Form.module.css';
import ReCAPTCHA from "react-google-recaptcha";
import axios from 'axios';
const Form = () => {
  

    const [inputValue1, setInputValue1] = useState('');
    const [inputValue2, setInputValue2] = useState('');
    const [inputValue3, setInputValue3] = useState('');
    const [inputValue4, setInputValue4] = useState('');


    return (
      <div>
        <form  class="contact-message-order-support-form contact-message-form contact-form" data-user-info-from-browser="" action="/support" method="post" id="contact-message-order-support-form" accept-charset="UTF-8" >
          <div class="field--type-string field--name-field-vashe-imya field--widget-string-textfield form-group js-form-wrapper form-wrapper" id="edit-field-vashe-imya-wrapper">
            <div class="form-item js-form-item form-type-textfield js-form-type-textfield form-item-field-vashe-imya-0-value js-form-item-field-vashe-imya-0-value form-group">
              <label for="edit-field-vashe-imya-0-value" class="control-label">Ваше имя</label>

              <input value={inputValue1} onChange={e => setInputValue1(e.target.value)} name="name" class="js-text-full text-full form-text form-control" type="text" id="edit-field-vashe-imya-0-value" size="60" maxlength="255" placeholder="Ваше имя" title="" data-toggle="tooltip" data-original-title="" />
            </div>
          </div>
          <div class="field--type-string field--name-field-telefon field--widget-string-textfield form-group js-form-wrapper form-wrapper" id="edit-field-telefon-wrapper">
            <div class="form-item js-form-item form-type-textfield js-form-type-textfield form-item-field-telefon-0-value js-form-item-field-telefon-0-value form-group">
              <label for="edit-field-telefon-0-value" class="control-label js-form-required form-required">Телефон</label>
              <input value={inputValue2} onChange={a => setInputValue2(a.target.value)} name="phone" class="js-text-full text-full form-text required form-control" type="text" id="edit-field-telefon-0-value" size="60" maxlength="255" placeholder="Телефон" required="required" aria-required="true" title="" data-toggle="tooltip" data-original-title="" />
            </div>

          </div>
          <div class="field--type-email field--name-field-e-mail field--widget-email-default form-group js-form-wrapper form-wrapper" id="edit-field-e-mail-wrapper">
            <div class="form-item js-form-item form-type-email js-form-type-email form-item-field-e-mail-0-value js-form-item-field-e-mail-0-value form-group">
              <label for="edit-field-e-mail-0-value" class="control-label js-form-required form-required">E-mail</label>
              <input value={inputValue3} onChange={b => setInputValue3(b.target.value)} name="email" class="form-email required form-control" type="email" id="edit-field-e-mail-0-value" size="60" maxlength="254" placeholder="E-mail" required="required" aria-required="true" title="" data-toggle="tooltip" data-original-title="" />
            </div>
          </div>
          <div class="field--type-string-long field--name-field-vash- field--widget-string-textarea form-group js-form-wrapper form-wrapper" id="edit-field-vash-wrapper">
            <div class="form-item js-form-item form-type-textarea js-form-type-textarea form-item-field-vash--0-value js-form-item-field-vash--0-value">
              <label for="edit-field-vash-0-value" class="control-label">Ваш комментарий</label>
              <div class="form-textarea-wrapper">
                <textarea value={inputValue4} onChange={c => setInputValue4(c.target.value)} class="js-text-full text-full form-textarea form-control resize-vertical" name="comment" title="" data-toggle="tooltip" id="edit-field-vash-0-value" rows="5" cols="60" placeholder="Ваш комментарий" data-original-title=""></textarea>
              </div>
            </div>

          </div>
          <input autocomplete="off" type="hidden" name="form_build_id" value="form-e0B6ZJT1Riq8lfePvZ9tRN0iPWPd1hkru01WWLV7Ekc" />
          <input type="hidden" name="form_id" value="contact_message_order_support_form" />
          <div class="form-item js-form-item form-type-checkbox js-form-type-checkbox form-item-fz152-agreement js-form-item-fz152-agreement checkbox">
            <label for="edit-fz152-agreement" class="control-label option js-form-required form-required">
              <input required="required" class="form-checkbox required" type="checkbox" id="edit-fz152-agreement" name="fz152_agreement" value="1" aria-required="true" /><span class="custom-checkbox"></span>Отправляя заявку, я даю согласие на <a href="/privacy-policy" target="_blank" rel="nofollow">обработку своих персональных данных</a>.</label>
          </div>
          <button 
          onClick= {async () => {
            try {
              await axios({
                url: "https://formcarry.com/s/1YIZ9cDApe",
                headers: {
                  "Content-type": "application/json"
                },
                params: {
                  name: inputValue1,
                  phone: inputValue2,
                  email: inputValue3,
                  comment: inputValue4
                },
                method: "POST",
                data: null
              }).then(({ data }) => {
                return data;
              });
            } catch (e) {
              console.log("Sending error", e);
            }
          }}
          class="button button--primary js-form-submit form-submit btn-primary btn" 
           disabled={inputValue1.length === 0 || inputValue2.length === 0 || inputValue3.length === 0 || inputValue4.length === 0}
            type="button" id="edit-submit" >Свяжитесь с нами</button>

        </form>

      </div>
    );
  }
  export default Form;